.header {
  width: 100vw;
  justify-content: center;
  display: flex;
}

.slider-container {
  height: 1104px;
  width: 100%;
  z-index: 2;
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.slider-container__wrapper {
  width: 375px;
  height: 100%;
  padding-left: 30px;
}

.slider-container__wrap-sc {
  width: 314px;
