  row-gap: 32px;
  padding-top: 35px;
  padding-left: 32px;
  display: flex;
}

.div-sc__wrap-info {
  margin-top: 45px;
  display: flex;
}

.wrap-info__title {
  width: 314px;
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 8px;
  font-family: Roboto;
  font-size: 59px;
  font-weight: 900;
  line-height: 1;
}

.wrap-info__button {
  color: rgba(255, 255, 255, .8);
  background: var(--Main-Red, #dd2242);
  width: 315px;
  height: 59px;
  text-align: center;
  border: none;
  border-radius: 108px;
  font-family: DM Sans;
  font-size: 18px;
  font-weight: 700;
  line-height: 150%;
}

.info-item__svg {
