  letter-spacing: .5px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
}

.slider {
  height: 1104px;
  width: 100%;
  z-index: 4;
  flex-direction: column;
  align-items: center;
  transition: transform .5s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.slide {
  height: 100%;
}

.video-div {
  height: 191px;
  width: 314px;
  z-index: 6;
  order: 3;
  align-items: center;
  display: flex;
  position: relative;
